import React, {
    useContext, useEffect, useState,
    Fragment
} from "react";

import {
    Dialog, DialogTitle, DialogContent,
    DialogContentText, DialogActions,
    Button, withMobileDialog, makeStyles,
    Stepper, Step, StepLabel, Grid
} from "@material-ui/core";
import { Alert } from "@material-ui/lab";

import moment from "moment";
import { useSnackbar } from "notistack";
import { sendWSMessage } from "../../../websocket/monitoring_ws";

import AlertReleaseForm from "./AlertReleaseForm";
import { getLatestSiteEventDetails, processReleaseInternalAlert } from "./ajax";
import { MonitoringShiftsContext } from "../../contexts/MonitoringShiftsContext";
import { WebsocketContext } from "../../contexts/WebsocketContext";

import { Store } from "./store";
import Actions from "./actions";


const useStyles = makeStyles(() => ({
    stepper: {
        padding: "12px 0 36px"
    }
}));

function AlertReleaseFormModal (props) {
    const {
        fullScreen, isOpen, closeHandler,
        chosenCandidateAlert, setChosenCandidateAlert,
        setIsOpenRoutineModal, generatedAlerts
    } = props;
    const classes = useStyles();

    const { current_ct } = useContext(MonitoringShiftsContext);
    const { enqueueSnackbar } = useSnackbar();
    const { candidateAlertsData } = useContext(WebsocketContext);

    const { 
        state, dispatch, validateIfNextIsDisabled,
        prepareAlertReleasePayload
    } = useContext(Store);
    useEffect(() => {
        if (isOpen) {
            const payload = moment();
            Actions.updateReleaseTime({ dispatch, payload });

            if (current_ct) {
                Actions.updateCTPersonnel({ dispatch, payload: current_ct.user_id });
            }
        }
    }, [isOpen, dispatch, current_ct]);

    useEffect(() => {
        if (chosenCandidateAlert && chosenCandidateAlert.general_status !== "routine") {
            setActiveStep(2);
            Actions.useCandidateAlert({ dispatch, payload: chosenCandidateAlert });
        }
    }, [chosenCandidateAlert, dispatch]);

    const [active_step, setActiveStep] = useState(0);
    const [is_wrong_release_data, setIsWrongReleaseData] = useState(true);
    const [wrong_datas, setWrongDatas] = useState([]);
    const [alert_variant, setAlertVariant] = useState("info");

    useEffect(() => {
        console.log("STATE", state);
        const { data_ts, site, preview } = state;
        const allowed_minutes = [0, 30];
        const allowed_hour = [3, 7, 11, 15, 19, 23];
        const current_data_ts = moment(data_ts);
        const data_ts_minute = moment(data_ts).minute();
        const data_ts_hour = moment(data_ts).hour();
        const temp_wrong_datas = [];
        console.log("candidateAlertsData", candidateAlertsData);
        let site_id;
        if (data_ts && site) {
            let site_data;
            let prev_public_alert;
            let monitoring_status;
            let prev_data_ts;
            if (site) {
                const { value } = site;
                site_id = value;
                site_data = candidateAlertsData.filter(e => e.site_id === site_id); 
                console.log(site_data);
                if (site_data.length > 0) {
                    const { previous_release, general_status } = site_data[0];
                    if (previous_release) {
                        const {
                            event_alert, data_ts: previous_data_ts
                        } = previous_release;
                        prev_data_ts = previous_data_ts;
                        if (event_alert) {
                            const {
                                public_alert_symbol
                            } = event_alert;
                            const { alert_level } = public_alert_symbol;
                            prev_public_alert = alert_level;
                        }
                        console.log(previous_release);
                        monitoring_status = general_status;
                    }
                    
                }
            }

            if (monitoring_status === "on-going" || monitoring_status === "lowering") {
                if (!allowed_minutes.includes(data_ts_minute) || !allowed_hour.includes(data_ts_hour)) {
                    temp_wrong_datas.push("Data Timestamp.");
                }
            } else if (monitoring_status === "onset" && !allowed_minutes.includes(data_ts_minute)) {
                temp_wrong_datas.push("Data Timestamp.");
            }

            if (monitoring_status === "on-going" && prev_data_ts) {
                const hours_diff = moment(current_data_ts).diff(prev_data_ts, "hours");
                console.log("HERE", hours_diff);
                if (hours_diff > 4) {
                    temp_wrong_datas.push(`No release for ${moment(prev_data_ts).add(4, "hours")
                    .add(30, "minutes")
                    .format("h a")}`);
                }
            }
        }
        setWrongDatas(temp_wrong_datas);
        console.log(temp_wrong_datas);
    }, [active_step, state]);

    const steps = [
        "General monitoring alert details",
        "Site alert data and triggers",
        "Alert release preview and comments"
    ];

    const [is_next_btn_disabled, isNextButtonDisabled] = useState(true);
    useEffect(() => {
        const temp = validateIfNextIsDisabled(active_step);
        isNextButtonDisabled(temp);
    }, [state, active_step, validateIfNextIsDisabled]);

    const [is_loading, setIsLoading] = useState(false);
    const handleNext = () => {
        if (active_step === 0) {
            setIsLoading(true);
            getLatestSiteEventDetails(state.site.value, result => {
                Actions.saveLatestSiteEventDetail({ dispatch, payload: result });

                // Check if has MOMS manually (m only) if it's manual release
                // Just in case it's raised manually and MOMS is only ground 
                // data presence needed for lowering
                if (!chosenCandidateAlert) {
                    const site_row = generatedAlerts.find(x => x.site_id === state.site.value);

                    if (site_row) {
                        const { unreleased_moms_list } = site_row;
                        const m_list = unreleased_moms_list.filter(x => x.op_trigger === 0);
                        Actions.saveNonTriggeringMoms({ dispatch, payload: m_list });
                    }
                }

                setIsLoading(false);
            });
        } else if (active_step === 1) {
            setIsLoading(true);
            processReleaseInternalAlert(state, result => {
                Actions.savePostComputationDetails({ dispatch, payload: result });
                setIsLoading(false);
            });
        } else {
            const snackbar_key = enqueueSnackbar(
                "Inserting EWI release...",
                {
                    variant: "warning",
                    persist: true
                }
            );

            const payload = prepareAlertReleasePayload(state);
            payload.snackbar_key = snackbar_key;
            console.log(payload);
            sendWSMessage("insert_ewi", payload);
            closeHandler();
            handleReset();
            return;
        }

        setActiveStep(active_step + 1);
    };
    
    const handleReset = () => {
        Actions.reset({ dispatch });
        setActiveStep(0);
        setChosenCandidateAlert(null);
        setIsLoading(false);
    };

    return (
        <Dialog
            fullWidth
            maxWidth="md"
            fullScreen={fullScreen}
            open={isOpen}
            aria-labelledby="form-dialog-title"
        >
            <DialogTitle id="form-dialog-title">Monitoring Alert Release Form</DialogTitle>
            <DialogContent>
                <DialogContentText>                    
                    Fill out the form and read the instructions carefully.
                </DialogContentText>

                <Stepper activeStep={active_step} alternativeLabel className={classes.stepper}>
                    { steps.map(label => (<Step key={label}>
                        <StepLabel>{label}</StepLabel>
                    </Step>)) }
                </Stepper>
                {
                    wrong_datas.length > 0 && (
                        <Grid container style={{ marginTop: 10, marginBottom: 10 }}>
                            <Grid item xs={12} md={4} />
                            <Grid item xs={12} md={4}>
                                <Alert severity={`${alert_variant}`}>
                                    Please check the following:<br/>
                                    {
                                        wrong_datas.map((row, index) => (
                                            <Fragment key={index}>
                                                <b>{index + 1}.</b> {row}<br/>
                                            </Fragment>
                                        ))
                                    }
                                </Alert>
                            </Grid>
                        </Grid> 
                    )
                }        
                <AlertReleaseForm 
                    activeStep={active_step}
                    isLoading={is_loading}
                />
            </DialogContent>
            <DialogActions>
                <Grid item xs style={{ 
                    display: active_step === 0 && 
                    chosenCandidateAlert === null ? "flex" : "none" 
                }}>
                    <Button onClick={setIsOpenRoutineModal} color="secondary">
                        Release Routine Manually
                    </Button>
                </Grid>
                                
                <Grid item xs align="right">
                    {
                        active_step < 3 && <Button 
                            onClick={handleReset}
                            color="primary"
                        >
                            Reset
                        </Button>
                    }
                    <Button 
                        onClick={closeHandler}
                        color="primary"
                    >
                        Close
                    </Button>
                    {
                        active_step < 3 && <Fragment>
                            <Button
                                disabled={active_step === 0} 
                                onClick={() => setActiveStep(active_step - 1)}
                            >
                                Back
                            </Button>
                            {
                                wrong_datas.length > 0 ? (
                                    <Button disabled>{active_step === steps.length - 1 ? "Submit" : "Next"}</Button>
                                ) : (
                                    <Button
                                        color="primary"
                                        onClick={handleNext}
                                        disabled={is_next_btn_disabled}
                                    >
                                        {active_step === steps.length - 1 ? "Submit" : "Next"}
                                    </Button>
                                )
                            }
                        </Fragment>
                    }
                </Grid>
            </DialogActions>
        </Dialog>
    );
}

export default withMobileDialog()(AlertReleaseFormModal);
