import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import QATable from "./Table";

const useStyles = makeStyles((theme) => ({
    root: {
        width: "100%",
    },
}));

export default function SMSInbox (props) {
    const classes = useStyles();
    const { inboxData, isLoading } = props;
    const columns = [
        {
            name: "timestamp",
            label: "Timestamp",
            options: {
                filter: true,
                sort: true,
            }
        },
        {
            name: "site_code",
            label: "Site Code",
            options: {
                filter: true,
                sort: true
            }

        },
        {
            name: "affiliation",
            label: "Affiliation",
            options: {
                filter: false,
                sort: false
            }

        },
        {
            name: "sender",
            label: "Sender",
            options: {
                filter: false,
                sort: false
            }

        },
        {
            name: "message",
            label: "Message",
            options: {
                filter: false,
                sort: false
            }
        },
        {
            name: "tag",
            label: "Tag",
            options: {
                filter: false,
                sort: false
            }
        },
    ];

    return (
        <div className={classes.root}>
            <QATable 
                isLoading={isLoading} 
                tableTitle="QA for Event Monitoring" 
                type="SMS Inbox"
                datas={inboxData}
                columns={columns}
            />
        </div>
    );
}